import { on, off } from '../../../utils/dom'
import { addResizeListener, removeResizeListener } from '../../../utils/resize-event'
import { scrollBarWidth, toObject } from './util'
import Bar from './bar'

export default {
  name: 'BScrollbar',
  components: { Bar },
  props: {
    normal: {
      type: Boolean,
      default: false
    }, // 
    always: { // 
      type: Boolean,
      default: false
    },
    wrapStyle: {}, //  wrap
    wrapClass: {}, //  wrap
    viewClass: {}, //  view
    viewStyle: {}, //  view
    barStyle: {}, // bar
    barWrapStyle: {}, // bar
    noResize: Boolean, //  container 
    tag: { // viewdiv
      type: String,
      default: 'div'
    },
    calcBarWidth: Boolean
  },
  data () {
    return {
      sizeWidth: '0',
      sizeHeight: '0',
      moveX: 0,
      moveY: 0
    }
  },
  computed: {
    wrap () {
      return this.$refs.wrap
    }
  },
  render (h) {
    // scrollBarWidth() // 17
    let gutter = 17
    if (this.calcBarWidth) {
      gutter = scrollBarWidth()
    }
    let style = this.wrapStyle
    // 
    if (gutter) {
      const gutterWith = `-${gutter}px`
      const gutterStyle = `margin-bottom: ${gutterWith}; margin-right: ${gutterWith};`
      // margin
      if (Array.isArray(this.wrapStyle)) {
        style = toObject(this.wrapStyle)
        style.marginRight = style.marginBottom = gutterWith
      } else if (typeof this.wrapStyle === 'string') {
        style += gutterStyle
      } else {
        style = gutterStyle
      }
    }
    // slots
    const view = h(this.tag, {
      class: ['bin-scrollbar__view', this.viewClass],
      style: this.viewStyle,
      ref: 'resize'
    }, this.$slots.default)
    // 
    const wrap = h(this.tag, {
      class: [this.wrapClass, 'bin-scrollbar__wrap', gutter ? '' : 'bin-scrollbar__wrap--hidden-default'],
      style: style,
      ref: 'wrap',
      on: {
        scroll: this.handleScroll
      }
    }, [view])

    let nodes
    // 
    if (!this.normal) {
      nodes = [wrap]
      if (this.sizeWidth) {
        nodes.push(
          h('bar', {
            class: [{ 'always': this.always }],
            props: {
              move: this.moveX,
              size: this.sizeWidth,
              barStyle: this.barStyle,
              barWrapStyle: this.barWrapStyle
            }
          })
        )
      }
      if (this.sizeHeight) {
        nodes.push(
          h('bar', {
            class: [{ 'always': this.always }],
            props: {
              vertical: true,
              move: this.moveY,
              size: this.sizeHeight,
              barStyle: this.barStyle,
              barWrapStyle: this.barWrapStyle
            }
          })
        )
      }
    } else {
      nodes = [
        h(this.tag, {
          class: [this.wrapClass, 'bin-scrollbar__wrap'],
          style: style,
          ref: 'wrap'
        }, [view])
      ]
    }
    return h('div', { class: 'bin-scrollbar' }, nodes)
  },
  methods: {
    handleScroll () {
      const wrap = this.wrap
      this.moveY = ((wrap.scrollTop * 100) / wrap.clientHeight)
      this.moveX = ((wrap.scrollLeft * 100) / wrap.clientWidth)
    },
    update () {
      let heightPercentage, widthPercentage
      const wrap = this.wrap
      if (!wrap) return
      // thumb
      heightPercentage = (wrap.clientHeight * 100 / wrap.scrollHeight)
      widthPercentage = (wrap.clientWidth * 100 / wrap.scrollWidth)
      // 100
      this.sizeHeight = (heightPercentage < 100) ? (heightPercentage + '%') : ''
      this.sizeWidth = (widthPercentage < 100) ? (widthPercentage + '%') : ''
    }
  },
  mounted () {
    if (this.normal) return

    this.updateEvent = this.$util.debounce(this.update, 10, false)
    this.$nextTick(this.update)
    if (!this.noResize) {
      on(window, 'resize', this.updateEvent)
      addResizeListener(this.$refs.resize, this.update) // 
    }
  },
  beforeDestroy () {
    if (this.normal) return
    if (!this.noResize) {
      off(window, 'resize', this.updateEvent)
      removeResizeListener(this.$refs.resize, this.update)
    }
  }
}
